/* General Style */
body {
    margin:0;
    line-height: 1.5;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    background-color: #f2f2fc;
    text-align: center;
}
*{
    margin:0;
    padding: 0;
    outline: none;
    text-decoration: none;
    box-sizing: border-box;
}

.hidden{
    display: none!important;
}
 /* background-color: blue; */


.chart-container{
    position: relative;
    /* height:20vh; */
    /* padding-left: 20%; */
    width:450px;
    margin: 0;
    display: flex;
    justify-content: center;
}
h1{
    padding-top: 5vh;
}

.skill-chart .skill-filter{
    padding-top: 10%;
    flex: 0 0 100%;
    max-width: 100%;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 40px;
    display: flex;
    justify-items: center;
    
}

.skill-chart .skill-filter button{
    border:none;
    background-color: transparent;
    font-size: 15px;
    font-weight: 600;
    text-transform: capitalize;
    margin: 0 10px;
    color: #302e4d;
    cursor: pointer;
    border-bottom: 2px solid transparent;
    white-space: nowrap;
    padding-bottom: 2px;
    transition: all 0.3s ease;
}


/* Dark Body */
body.dark{
    /* Testing if this comment is added */
    background-color: #151515;
}
body.dark .section{
    background-color: #151515;
}
body.dark h1,
body.dark .skill-chart,
body.dark .skills .skill-item h5,
body.dark .skill-chart .skill-filter button{
    color: #ffffff;
}
body.dark .skills .skill-item .skill-percent{
    color: #e9e9e9;
}






